.login{
    height: calc(100vh - 70px);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: url("https://www.shutterstock.com/image-photo/old-brick-black-color-wall-260nw-1605128917.jpg");
    background-size: cover;
}
.loginTitle{
    font-size: 50px;
    color: aliceblue;
}
.loginform{
    margin-top: 20px;
    display: flex;
    flex-direction: column;
}
.loginform > label{
    margin-top: 10px 0;
    color: aliceblue;
}
.loginform>input{
    padding: 10px;
    background-color: aliceblue;
    border: none;
}
.loginButton{
    margin-top: 20px;
    cursor: pointer;
    background-color: aqua;
    border: none;
    border-radius: 10px;
    font-size: 22px;
    padding: 10px;
}
.loginRegisterButton{
    position: absolute;
    top: 60px;
    right: 20px;
    margin-top: 20px;
    cursor: pointer;
    background-color: rgb(0, 255, 94);
    border: none;
    border-radius: 10px;
    font-size: 22px;
    padding: 10px 20px;


}