.nav{
    width: 100%;
    height: 60px;
    background-color: aqua;
    position: sticky;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}
.navLeft,.navRight{
    flex: 3;
}
.navCenter{
    flex: 6;
}
.Icon{
    font-size: 25px;
    color: rgb(61, 43, 226);
    margin-left: 15px;
}
.NavList{
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 0;
    list-style: none;

}
.NavListItem{
    margin-right: 20px;
    font-size: 22px;
    font-weight: 800;
}
.IconLogout{
    margin-left: 10px;
    font-size: 24px;
}
.navRight{
    text-align: right;
    margin-right: 20px;
}